"use client";

type ShipmentCardProps = {
  trackingNumber: string;
  companyName: string;
  provider: string;
  price: number;
  currency: string;
  date: string; // už formátovaný text, třeba "9. 10. 2025"
  originCountry: string;
  destinationCountry: string;
  mode: "EXPORT" | "IMPORT";
  invoiceCount?: number;
};

export function ShipmentCard(props: ShipmentCardProps) {
  const {
    trackingNumber,
    companyName,
    provider,
    price,
    currency,
    date,
    originCountry,
    destinationCountry,
    mode,
    invoiceCount,
  } = props;

  const isExport = mode === "EXPORT";

  return (
    <div className="flex items-stretch gap-4 rounded-2xl bg-white shadow-sm px-4 py-3 border border-slate-100 hover:shadow-md transition-shadow">
      {/* "Logo" / provider */}
      <div className="flex items-center">
        <div className="text-2xl font-extrabold bg-linear-to-r from-purple-600 to-orange-500 bg-clip-text text-transparent">
          {provider}
        </div>
      </div>

      <div className="flex-1 flex flex-col justify-between">
        <div>
          <div className="text-xs text-slate-400 font-medium">TRK#</div>
          <div className="text-sm font-semibold text-slate-800">
            {trackingNumber}
          </div>
          <div className="text-xs text-slate-500">{companyName}</div>
        </div>

        <div>
          <div className="flex items-center gap-2">
            <span className="text-sm font-semibold text-emerald-600">
              {price.toFixed(2)} {currency}
            </span>
            {invoiceCount && invoiceCount > 1 && (
              <span className="inline-flex items-center rounded-full bg-slate-100 px-2 py-0.5 text-[10px] font-medium text-slate-600">
                invoices: {invoiceCount}
              </span>
            )}
          </div>
          <div className="mt-1 text-xs text-slate-500 flex items-center gap-1">
            <span className="inline-flex items-center justify-center rounded-full bg-slate-100 px-2 py-0.5 text-[10px] font-medium">
              {originCountry}
            </span>
            <span className="text-slate-400">→</span>
            <span className="inline-flex items-center justify-center rounded-full bg-slate-100 px-2 py-0.5 text-[10px] font-medium">
              {destinationCountry}
            </span>
          </div>
        </div>
      </div>

      <div className="flex flex-col justify-between items-end text-right">
        <div className="text-xs text-slate-500">{date}</div>
        <button
          className={`mt-2 inline-flex items-center rounded-full px-3 py-1 text-[11px] font-semibold ${
            isExport
              ? "bg-emerald-50 text-emerald-700 border border-emerald-100"
              : "bg-fuchsia-50 text-fuchsia-700 border border-fuchsia-100"
          }`}
        >
          {isExport ? "EXPORT" : "IMPORT"}
        </button>
      </div>
    </div>
  );
}
