"use client";

import { useState } from "react";
import { UploadInvoicesDialog } from "./components/UploadInvoiceDialog";
import { ShipmentCard } from "./components/ShipmentCard";

// dokud nemáme backend endpoint na shipments, použijeme mock data
type ShipmentMock = {
  id: string;
  trackingNumber: string;
  companyName: string;
  provider: string;
  price: number;
  currency: string;
  date: string;
  originCountry: string;
  destinationCountry: string;
  mode: "EXPORT" | "IMPORT";
  invoiceCount?: number;
};

const MOCK_SHIPMENTS: ShipmentMock[] = [
  {
    id: "1",
    trackingNumber: "885335999184",
    companyName: "Acme Corporation s.r.o.",
    provider: "FedEx",
    price: 457.24,
    currency: "Kč",
    date: "9. 10. 2025",
    originCountry: "CZ",
    destinationCountry: "UK",
    mode: "EXPORT",
    invoiceCount: 1,
  },
  {
    id: "2",
    trackingNumber: "885335999185",
    companyName: "Acme Corporation s.r.o.",
    provider: "FedEx",
    price: 457.24,
    currency: "Kč",
    date: "9. 10. 2025",
    originCountry: "UK",
    destinationCountry: "CZ",
    mode: "IMPORT",
    invoiceCount: 2,
  },
  {
    id: "3",
    trackingNumber: "885335999186",
    companyName: "Beta Logistics a.s.",
    provider: "GLS",
    price: 812.0,
    currency: "Kč",
    date: "10. 10. 2025",
    originCountry: "CZ",
    destinationCountry: "DE",
    mode: "EXPORT",
    invoiceCount: 3,
  },
  // … můžeš si dopsat víc, ať grid vypadá plnější
];

export default function DashboardPage() {
  const [companyFilter, setCompanyFilter] = useState<string>("");

  const filteredShipments = MOCK_SHIPMENTS.filter((s) =>
    companyFilter
      ? s.companyName.toLowerCase().includes(companyFilter.toLowerCase())
      : true
  );

  const handleUploaded = () => {
    // tady později uděláme refetch z backendu (GET /shipments)
    console.log("Invoices uploaded – tady v budoucnu refetch.");
  };

  return (
    <main className="min-h-screen bg-slate-100 flex justify-center">
      <div className="w-full max-w-6xl px-4 py-6">
        {/* Top bar */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-3">
            <input
              value={companyFilter}
              onChange={(e) => setCompanyFilter(e.target.value)}
              placeholder="Filter by company…"
              className="w-60 rounded-full border border-slate-200 bg-white px-3 py-2 text-sm text-slate-700 shadow-sm focus:outline-none focus:ring-2 focus:ring-emerald-500/40 focus:border-emerald-500"
            />
          </div>

          <UploadInvoicesDialog onUploaded={handleUploaded} />
        </div>

        {/* Grid of shipment cards */}
        <div className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-4">
          {filteredShipments.map((s) => (
            <ShipmentCard
              key={s.id}
              trackingNumber={s.trackingNumber}
              companyName={s.companyName}
              provider={s.provider}
              price={s.price}
              currency={s.currency}
              date={s.date}
              originCountry={s.originCountry}
              destinationCountry={s.destinationCountry}
              mode={s.mode}
              invoiceCount={s.invoiceCount}
            />
          ))}

          {filteredShipments.length === 0 && (
            <div className="col-span-full rounded-2xl bg-white border border-dashed border-slate-200 p-6 text-center text-sm text-slate-500">
              Žádné zásilky pro zadaný filtr. Zkus změnit company filter nebo
              nahrát nové faktury.
            </div>
          )}
        </div>
      </div>
    </main>
  );
}
